export function suspensionBall(dragId, dragLink) {
    var startEvt, moveEvt, endEvt
    // 
    if ('ontouchstart' in window) {
        startEvt = 'touchstart'
        moveEvt = 'touchmove'
        endEvt = 'touchend'
    } else {
        startEvt = 'mousedown'
        moveEvt = 'mousemove'
        endEvt = 'mouseup'
    }
    // 
    var drag = document.getElementById(dragId)
    drag.style.position = 'absolute'
    drag.style.cursor = 'pointer'
    // 
    var isClick = true
    var disX, disY, left, top, starX, starY

    drag.addEventListener(startEvt, function (e) {
        // 
        e.preventDefault()
        // IE
        var e = e || window.event
        isClick = true
        // 
        starX = e.touches ? e.touches[0].clientX : e.clientX
        starY = e.touches ? e.touches[0].clientY : e.clientY
        // 
        disX = starX - drag.offsetLeft
        disY = starY - drag.offsetTop
        // 
        document.addEventListener(moveEvt, moveFun)
        document.addEventListener(endEvt, endFun)
    })

    function moveFun(e) {
        // IE
        var e = e || window.event
        // 2020
        if (
            Math.abs(starX - (e.touches ? e.touches[0].clientX : e.clientX)) > 20 ||
            Math.abs(starY - (e.touches ? e.touches[0].clientY : e.clientY)) > 20
        ) {
            isClick = false
        }
        left = (e.touches ? e.touches[0].clientX : e.clientX) - disX
        top = (e.touches ? e.touches[0].clientY : e.clientY) - disY
        // X
        if (left < 0) {
            left = 0
        } else if (left > document.documentElement.clientWidth - drag.offsetWidth) {
            left = document.documentElement.clientWidth - drag.offsetWidth
        }
        // Y(110)
        if (top < 0) {
            top = 0
        } else if (top > document.documentElement.clientHeight - drag.offsetHeight - 110) {
            top = document.documentElement.clientHeight - drag.offsetHeight - 110
        }
        drag.style.left = left + 'px'
        drag.style.top = top + 'px'
    }

    function endFun(e) {
        document.removeEventListener(moveEvt, moveFun)
        document.removeEventListener(endEvt, endFun)
        // if (isClick) { // 
        //     document.getElementById('ballId').style.visibility = 'hidden'
        //     appear()
        // }
    }
}


